/** @jsxRuntime classic */
/** @jsx jsx */
import { css, jsx } from "@emotion/react";
import styled from '@emotion/styled';
import React, {useState} from "react";

const Container = styled.div`
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 30px 10px;
  background: #f0f5ff;
`

const Left = styled.div`
  flex: 3;
`
const Right = styled.div`
  flex: 2;
  display: flex;
  justify-content: center;
`

const SelectContainer = ({propsList,children}) => {
    const [cssProp,setCssProp] = useState(propsList[0]);

    const SelectItem = (prop,index)=>{
        return(
            <div
                onClick={()=>setCssProp(prop)}
                key={`cssProps-${index}`}
                css={css`
                  width: 100%;
                  height: 50px;
                  display: flex;
                  justify-content: space-between;
                  align-items: center;
                  cursor: pointer;
                  font-family : 'Shadows Into Light',serif;
                  border: ${prop===cssProp?'2px #2f54eb solid;':'2px #bfbfbf solid;'}
                  border-radius: 3px;
                  padding: 30px 10px;
                  margin: 10px;
                  background: aliceblue;
                  box-shadow: 0 0.125rem 0.25rem rgba(0, 0, 0, 0.075)
                `}
            >
                {prop}
            </div>
        )
    }

    return(
            <Container>
                <Left>
                    {propsList.map((prop,index)=>SelectItem(prop,index))}
                </Left>
                <Right>
                    {React.cloneElement(children, {cssProp})}
                </Right>
            </Container>
    )
};

export default SelectContainer;
